<template>
<div class="row justify-content-center">
    <div class="col-md-6">
        <h2>Edit Students</h2>
        <form @submit.prevent="handleUpdateForm">
            <div class="form-group">
                <label for="img">Picture</label>
                <input type="text" src="image" class="form-control" v-model="student.img" required>
            </div>
            <div class="form-group">
                <label for="name">Name</label>
                <input type="text" class="form-control" v-model="student.name" required>
            </div>
            <div class="form-group">
                <label for="email">Email</label>
                <input type="email" class="form-control" v-model="student.email" required>
            </div>
            <div class="form-group">
                <label for="phone">Phone</label>
                <input type="text" class="form-control" v-model="student.phone" required>
            </div>
            <div class="form-group">
                <button class="btn btn-success btn-block">Update</button>
            </div>
        </form>
    </div>
</div>
</template>

<script>
import axios from 'axios';

export default {
    data() {
        return {
            student: {
            }
    }
  },
  created() {
    let apiURL = `http://localhost:4000/edit-student/${this.$route.params.id}`;
    axios.get(apiURL).then((res) => {
        this.student =res.data
    })
  },methods: {
    handleUpdateForm() {
    let apiURL = `http://localhost:4000/update-student/${this.$route.params.id}`;
    axios.put(apiURL, this.student).then((res) => {
        console.log(res);
        this.$router.push('/view')
    }).catch(error => {
        console.log(error)
    })
    }
  }
 }
</script>